.log-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 14px;
}

.log-table th, .log-table td {
  border-bottom: 1px solid #e0e0e0;
  padding: 12px 15px;
  text-align: left;
}

.log-table th {
  background-color: #f9f9f9;
  color: #333;
  font-weight: 600;
}

.log-table tbody tr:hover {
  background-color: #f7f7f7;
}

.log-table td:last-child {
  font-weight: 600;
  color: #007bff;
  text-align: right;
}

.status-badge {
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
}

.status-badge.status-PAID {
  background-color: #e0f8e9; /* Açık Yeşil */
  color: #28a745; /* Koyu Yeşil */
}

.status-badge.status-PENDING {
  background-color: #fff8e1; /* Açık Sarı */
  color: #ffc107; /* Koyu Sarı */
}

/* Tablodaki İşlem Butonları */
.actions-cell {
  display: flex;
  gap: 8px;
  justify-content: flex-end;
}

.pay-button {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 13px;
  font-weight: 500;
}
.pay-button:hover {
  background-color: #218838;
}

/* ... (mevcut stiller) ... */

/* YENİ: Toplu işlem kutusu */
.batch-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  margin-bottom: 20px;
  background-color: #e6f7ff;
  border: 1px solid #b3e0ff;
}
.batch-actions p {
  margin: 0;
  font-weight: 500;
  color: #0056b3;
}

/* YENİ: Checkbox sütunu stilleri */
.checkbox-cell {
  width: 40px;
  text-align: center;
  padding-left: 15px;
  padding-right: 5px;
}
.checkbox-cell input[type="checkbox"] {
  width: 16px;
  height: 16px;
  cursor: pointer;
}

.log-table tbody tr.selected-row {
  background-color: #fff8e1;
}
.log-table tbody tr.selected-row:hover {
  background-color: #fff0c7;
}